@mixin light {
	font-family: 'Montserrat', Arial, sans-serif;
	font-weight: 300; }

@mixin regular {
	font-family: 'Montserrat', Arial, sans-serif;
	font-weight: 400; }

@mixin medium {
	font-family: 'Montserrat', Arial, sans-serif;
	font-weight: 500; }

@mixin semibold {
	font-family: 'Montserrat', Arial, sans-serif;
	font-weight: 600; }

@mixin bold {
	font-family: 'Montserrat', Arial, sans-serif;
	font-weight: 700; }

@mixin extrabold {
	font-family: 'Montserrat', Arial, sans-serif;
	font-weight: 800; }

@mixin black {
	font-family: 'Montserrat', Arial, sans-serif;
	font-weight: 900; }

.out {
	position: relative;
	z-index: 1;
	overflow: hidden; }

html {
	scroll-behavior: smooth; }

body {
	background: $mainBg;
	font-size: 14px;
	line-height: 1.2;
	color: $white;
	@include regular; }

a {
	color: $themeColor2;

	&:hover {
		text-decoration: none; } }

.text {
	letter-spacing: 0.7px;
	font-size: 22px;
	line-height: 1.7;
	text-align: center;
	//+laptop
	//ont-size: 18px
	@include tablet {
		font-size: 19px;
		letter-spacing: 1.1px;
		line-height: 1.8; }
	@include md {
		font-size: 16px;
		line-height: 1.8; }
	@include xs {
		font-size: 15px; }
	a {
		color: inherit; } }

.main {
	display: flex;
	flex-direction: column;
	&.blur {
		filter: blur(10px) grayscale(.6); }

	// &:before
	//ontent: ''
	//osition: fixed
	//eft: 0
	//ight: 0
	//op: 0
	//ottom: 0
	//ackground: url(../img/overlay.png) center top repeat
	//-index: 1
 }	//ointer-events: none

.title-gradient {
	letter-spacing: 0.01em;
	text-align: center;
	font-size: 92px;
	@include bold;
	line-height: 1.07;
	text-transform: uppercase;
	color: $themeColor;

	@include ie {
		color: #00dd3b;
		background: transparent;
		.leaf {
			color: #00dd3b;
			background: transparent; } }
	@include xxl {
		font-size: 5.5vw; }

	@include laptop {
		font-size: 65px;
		font-size: 5.7vw; }
	//+tablet
	//ont-size: 46px
	@include md {
		font-size: 60px;
		font-size: 6.4vw; }
	@include sm {
		font-size: 36px;
		font-size: 6vw; }
	@include xs {
		font-size: 34px; }

	em {
		position: relative;
		font-style: normal;
		color: $themeColor;

		&:after {
			content: '';
			position: absolute;
			left: 50%;
			top: 50%;
			transform: translate(-50%, -50%);
			width: 684px;
			height: 700px;
			margin: -7px 0 0 -5px;
			background: url(../img/event-bg.svg) center center no-repeat;
			z-index: -1; } }

	strong {
		position: relative;
		background: linear-gradient(120deg,$themeColor,$themeColorD);
		-webkit-background-clip: text;
		color: #161917;
		-webkit-text-stroke: 5px transparent;

		&:after {
			content: "";
			position: absolute;
			width: 69%;
			height: 50%;
			left: 50%;
			top: 50%;
			-webkit-transform: translate(-46%,-162%);
			transform: translate(-46%,-162%);
			background: url(../img/cup.svg) 50% no-repeat;
			background-size: 100% 100%; } } }

.wrapper-link {
	text-decoration: none;
	color: unset; }

.separator {
	height: 48px;
	background: #f00;
	margin: 0 0 -48px;
	background: url(../img/separator.svg); }

.effect_sm {
	text-shadow: 1px 1px $themeColor, -1px -1px $themeColor2; }

.effect {
	text-shadow: 2px 2px $themeColor, -2px -2px $themeColor2; }

p,
a,
h1,
h2,
h3,
h4,
h5,
h6 {
	position: relative;
	z-index: 1; }
