.hero {
	position: relative;
	min-height: 100vh;
	//min-height: calc(var(--vh, 1vh) * 100)
	padding: 30px 0 80px;
	overflow: hidden;
	display: flex;
	justify-content: center;
	align-items: center;
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	//background-image: url(../img/hero-overlay.png)
	background-color: #1e2220;
	z-index: 1;
	pointer-events: none;

	&:before {
		content: '';
		position: fixed;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		background: url(../img/overlay.png) center top repeat;
		z-index: 1;
		pointer-events: none; }

	&:after {
		content: '';
		@include coverdiv;
		z-index: 1;
		background-color: #000;
		opacity: 0.35; }

	@include tablet {
		padding: 45px 0 105px; }
	@include md {
		padding: 60px 0 80px; }
	@include sm {
		padding: 20px 0 60px; }
	@include r(380) {
		@include rhmin(600) {
			padding: 20px 0 40px!important;
			.hero__logo, .h-list {
				margin-bottom: 25px; } } }
	&__video {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		min-width: 100%;
		min-height: 100%;
		width: auto;
		height: auto;
		z-index: -1;
		@include r(899) {
			display: none; } }

	&__bg {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		min-width: 100%;
		min-height: 100%;
		width: auto;
		height: auto;
		z-index: -1;
		// background: url(../img/screens-done-op.svg) center center no-repeat
		background-size: cover;
		background: #151816; }

	&__inner {
		width: 1566px;
		//margin: 0 auto
		padding: 0 20px;
		position: relative;
		z-index: 2;
		pointer-events: none;
		@include r(1460) {
			padding: 0 80px; }
		@include r(1279) {
			padding: 0 40px; }
		@include sm {
			padding: 0 20px; }
		@include xs {
			padding: 0 15px; } }

	&__logo {
		width: 324px;
		margin: 0 auto 78px;

		@include tablet {
			margin: 0 auto 40px;
			width: 260px; }

		@include md {
			width: 280px;
			margin: 0 auto 30px; }
		@include xs {
			margin: 30px auto 26px;
			width: 200px;
			height: auto; } }

	&__suptitle {
		font-size: 18px;
		margin-bottom: 68px;
		text-align: center;
		@extend .effect_sm;

		@include tablet {
			font-size: 18px;
			margin-bottom: 40px; }
		@include md {
			margin-bottom: 30px; }
		@include xs {
			font-size: 15px;
			margin: 0 -10px 30px; } }

	&__title {
		font-size: 52px;
		line-height: 1.1;
		text-align: center;
		margin-bottom: 68px;
		letter-spacing: -0.02em;
		@extend .effect;
		@include extrabold;
		@include laptop {
			font-size: 52px; }


		@include tablet {
			font-size: 5vw;
			line-height: 1.11;
			letter-spacing: 0.03em;
			margin-bottom: 40px;
			margin-left: -30px;
			margin-right: -30px; }
		@include md {
			font-size: 5vw;
			line-height: 1;
			margin-bottom: 32px; }
		@include sm {
			font-size: 6vw;
			margin-bottom: 20px;
			margin-left: 0;
			margin-right: 0; }
		@include xs {
			font-size: 36px;
			margin: 0 auto 20px;
			line-height: 1.16;
			max-width: calc(100% - 5px); } }

	&__btn {
		//max-width: 283px
		//margin: 0 auto
		display: flex;
		justify-content: center;
		flex-wrap: wrap;

		.btn {
			pointer-events: auto;
			&:not(:first-child) {
				margin: 0 0 0 20px; } }

		@include sm {
			flex-direction: column;

			.btn {
				margin: 10px auto 0;

				&:not(:first-child) {
					margin: 10px auto 0; } } } }

	&__partner {
		position: absolute;
		right: 10px;
		top: 0;
		font-size: 11px;
		line-height: 1.4;
		pointer-events: auto;

		@include tablet {
			font-size: 14px;
			right: 20px; }

		@include md {
			display: flex;
			flex-direction: column;
			align-items: center;
			position: relative;
			right: 0;
			margin: 20px auto; } }

	&__partners {
		display: flex;
		margin-top: 4px;

		@include tablet {
			margin-top: 5px; } }

	&__partner-img {
		display: flex;
		align-items: center;
		justify-content: center;

		&:not(:first-child) {
			margin: 0 0 0 20px;

			@include sm {
				margin: 0 0 0 10px; } }

		img {
			height: 60px;
			width: auto; } }

	&__timer {
		position: absolute;
		padding: 0 2vw;
		right: 0;
		left: 0;
		bottom: 1vw;
		font-size: 15vw;
		@include black;
		color: $themeColor2;
		opacity: .25;
		line-height: 1;
		text-align: left;
		white-space: nowrap;

		.live {
			text-align: center; } }

	&_live {
		align-items: flex-start;
		height: 150px;
		min-height: 0;
		padding: 20px 0;

		@include tablet {
			padding: 20px 0; }
		@include md {
			padding: 20px 0;
			height: 120px; }
		@include sm {
			padding: 10px 0; }
		@include xs {
			height: auto;
			padding: 10px 0 !important; }


		.container {
			position: relative;
			z-index: 2;
			pointer-events: auto;
			width: 100%;
			height: 100%;
			padding: 0 80px;

			@include sm {
				padding: 0 60px; } }

		.hero {
			&__bg {
				z-index: -1;
				background: url(../img/screens-done-op.svg) center top no-repeat;
				background-size: cover; }

			&__inner {
				position: relative;
				width: 100%;
				height: 100%;
				padding: 0;
				margin: 0;
				display: flex;
				justify-content: space-between;

				@include md {
					margin: 0; }

				@include xs {
					flex-direction: column-reverse;
					align-items: center; } }

			&__date {
				font-size: 18px;
				margin: auto 0;

				@include md {
					font-size: 14px; }

				@include xs {
					font-size: 12px;
					margin: 15px 0 10px; } }

			&__logo {
				width: 200px;
				position: absolute;
				left: 50%;
				top: 50%;
				transform: translate(-50%, -50%);
				margin: 0;

				@include md {
					width: 150px;
					margin: 0px auto 0; }

				@include xs {
					position: relative;
					left: 0;
					top: 0;
					transform: translate(0, 0);
					width: 130px;
					margin: 0px auto 0; } }

			&__partner {
				position: relative;
				height: auto;
				margin: auto 0 auto auto;
				top: 0;
				right: 0;

				@include tablet {
					font-size: 10px; }

				@include md {
					margin: auto 0 auto;

					img {
						height: 30px; } }

				@include xs {
					display: none; }

				img {
					height: 50px; } }

			&__partners {
				@include md {
					margin: 0; } }



			&__partner-text {
				@include sm {
					display: none; } } } } }


.h-list {
	display: flex;
	justify-content: space-between;
	max-width: 1100px;
	margin: 0 auto 152px;
	padding: 0 50px;

	&_min {
		max-width: 411px; }
	@include tablet {
		margin: 0 auto 52px; }
	@include md {
		max-width: 600px;
		margin-bottom: 65px;
		padding: 0; }
	@include xs {
		flex-wrap: wrap;
		justify-content: center;
		padding: 0;
		margin-bottom: 45px; }
	//max-width: 230px
	&__item {
		display: flex;

		@include md {
			flex-direction: column;
			text-align: center; }

		@include sm {
			width: 50%;
			align-items: center;
			text-align: center; }
		&:not(:last-child) {
			margin-right: 40px;
			@include sm {
				margin-right: 0; } }
		@include xs {
			margin-bottom: 9px; }
		&:nth-child(2n) {
			@include xs {
				margin-right: 0; } } }
	//+xs
 //     margin-right: 0
 //     //margin-bottom: 20px
 //     margin-bottom: 15px
	//&_sm
 //     @include xs
 //         max-width: 90px
	//&_lg
 //     @include xs
 //         width: auto
 //         max-width: unset

	&__value {
		font-size: 52px;
		margin: 0 20px 0 0;
		@extend .effect;
		@include bold;
		display: flex;
		align-items: center;
		@include md {
			margin: 0;
			justify-content: center; }
		@include sm {
			justify-content: flex-start; }
		@include xs {
			margin-bottom: 0;
			font-size: 48px;
			line-height: 1.1; }

		.icon-infinity {
			width: 71px;
			height: 60px;
			fill: #FFF; } }

	&__text {
		font-size: 14px;
		line-height: 1.4;
		padding: 6px 0;
		@extend .effect_sm;
		&-top {
			font-size: 20px;
			@include medium;
			text-transform: uppercase;
			@include tablet {
				font-size: 16px; }
			@include xs {
				font-size: 14px; } }
		@include xs {
			font-size: 12px; }
		@include tablet {
			padding: 9px 0; }
		@include md {
			padding: 2px 0; } }
	&__icon {
		margin-top: 10px;
		margin-left: 8px;
		font-size: 30px;
		&_anim {
			animation: heart 2s infinite; }
		.icon {
			fill: $white; }
		@include sm {
			margin-top: 6px;
			.icon {
				width: 0.9em; } } } }



@keyframes heart {
	0% {
		transform: scale(1); }
	50% {
		transform: scale(1.2); }
	100% {
		transform: scale(1); } }


.logo {
	display: block;
	//height: 100%
	width: 100%;
	pointer-events: auto;
	img {
		//height: 100%
		width: 100%; } }


