.c-form {
	padding: 120px 0 120px;
	@include laptop {
		padding: 90px 0 90px; }
	@include md {
		padding: 60px 0 60px; }
	@include xs {
		padding: 10px 0 10px; }
	.section__title {
		letter-spacing: 1.3px;
		margin-bottom: 55px;
		@include tablet {
			margin-bottom: 37px; } }
	.container {
		@include xs {
			padding: 0 10px; } }
	&__inner {
		position: relative;
		padding: 90px 40px 100px;
		display: flex;
		flex-direction: column;
		align-items: center;
		box-shadow: 0 0 150px 10px rgba(255, 0, 248, 0.2);
		border-radius: 25px;

		&:before {
			content: '';
			position: absolute;
			left: 0;
			top: 0;
			right: 0;
			bottom: 0;
			background: linear-gradient(to left,$themeColor,$themeColor2);
			z-index: -2;
			border-radius: 25px; }

		&:after {
			content: '';
			position: absolute;
			left: 15px;
			top: 15px;
			right: 15px;
			bottom: 15px;
			background: $mainBg;
			z-index: -1;
			border-radius: 13px; }

		@include tablet {
			padding: 80px 40px; }

		@include md {
			padding: 50px 40px; }

		@include xs {
			padding: 40px 40px; } }
	&__socials {
		margin-bottom: 55px;
		@include tablet {
			margin-bottom: 30px; }
		@include sm {

			.social__item {
				svg {
					font-size: 25px; }
				&:not(:last-child) {
					margin-right: 20px; } } } }
	&__text {
		line-height: 1.6;
		letter-spacing: 0.003em;
		max-width: 540px;
		text-align: center;
		font-size: 18px;
		margin-bottom: 42px;
		@include tablet {
			font-size: 16px;
			margin-bottom: 38px; } }
	&__form {
		width: 100%;
		max-width: 762px; }
	&__field {
		position: relative;
		width: 100%;
		z-index: 5;
		&:hover {
			input {
				border-bottom-color: #0f0; } }
		input {
			@include regular;
			width: 100%;
			display: block;
			font-size: 22px;
			color: $white;
			background-color: $mainBg;
			padding: 20px 120px 20px 25px;
			border-radius: 5px;
			@include placeholder-color($white);
			transition: border-color .3s ease;
			&:hover, &:active, &:focus {
				border-bottom-color: #0f0; }
			&::placeholder {
				transition: color .3s ease; }
			@include tablet {
				font-size: 24px;
				padding: 20px 120px 20px 20px; }
			@include xs {
				font-size: 16px;
				padding: 12px 80px 14px 10px; } } }


	&__submit {
		display: flex;
		align-items: center;
		position: absolute;
		right: 0;
		top: 0;
		bottom: 0;
		padding: 0 20px;
		align-items: center;
		background-color: transparent;
		font-size: 14px;
		text-transform: uppercase;
		color: $white;
		background: $themeColorD;
		border-radius: 5px;
		@include xs {
			font-size: 12px;
			transform: none; }

		@include xxs {
			.icon {
				margin: 0; }
			span {
				display: none; } }

		&:hover {
			.icon {
				transform: translateX(5px); } }
		.icon {
			transition: 0.3s;
			margin-left: 13px;
			fill: $white;
			font-size: 20px; } } }
